.form-container {
  flex-grow: 1;
  margin: 0 20px;

  @media (min-width: @tablet-width) {
    margin: 0 60px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 120px;
  }
}

.form-container__personal-data-wrapper {
  margin-bottom: 22px;

  @media (min-width: @tablet-width) {
    margin-bottom: 61px;
  }
}

.form-container__header-wrapper {
  position: relative;
  margin: 36px 0 53px;

  @media (min-width: @tablet-width) {
    margin: 64px 106px 107px;
  }

  @media (min-width: @desktop-width) {
    margin: 115px 262px 68px;
  }
}

.form-container__wrapper {
  margin-bottom: 59px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 58px;
    margin-bottom: 63px;
  }

  @media (min-width: @desktop-width) {
    gap: 100px;
    margin-bottom: 71px;
  }
}

.form-container__legend,
.form-container__lead {
  .font-size-lead-mobile();
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}

.form-container__legend {
  padding: 0;
  margin-bottom: 20px;

  @media (min-width: @tablet-width) {
    .font-size-lead();
    margin-bottom: 28px;
  }
}

.form-container__lead {
  margin-bottom: 24px;

  @media (min-width: @tablet-width) {
    .font-size-title();
    margin-bottom: 21px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 31px;
  }
}

.form-container__description {
  color: @dark-gray;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}

.form-container__group {
  position: relative;
  border: none;
  margin: 0;
  padding: 0;
}

@media (max-width: @mobile-width-only) {
  .form-container__group::before {
    content: "";
    position: absolute;
    top: -70px;
    left: -20px;
    right: -20px;
    border-top: 1px solid @disabled;
  }
}

.form-container__button-wrapper {
  display: grid;

  @media (min-width: @tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    margin: 0 96px;
  }
}

.form-container__button {
  .button();
  margin-bottom: 3px;
}

.form-container__footnote {
  font-size: 12px;
  text-transform: uppercase;
  text-align: end;
  margin: 0;
  opacity: 0.6;


  @media (min-width: @tablet-width) {
    .font-size-description();
    margin-top: 6px;
  }
}
